import LabelGroup from "./LabelGroup";
import moment from "moment";
import { useEffect, useRef, useState } from "react";

function PurchaseEPinInfo(props) {
  const [block, setBlock] = useState(0);
  const timer = useRef(null);

  useEffect(() => {
    if (block > 0) {
      timer.current = setTimeout(() => {
        let _block = block - 1;
        setBlock(_block);
      }, 1000);
    }
    return () => {
      if (timer.current != null) clearTimeout(timer.current);
    };
  });

  function blockCheck(event) {
    event.preventDefault();
    setBlock(15);
  }

  return (
    <div className="card border-0 rounded-lg shadow-cm mb-3 col-md-12">
      <div className="card-body px-2">
        <LabelGroup
          label="Purchase ID"
          // eslint-disable-next-line no-undef
          value={props.info.purchaseId.replace(/\B(?=(\d{4})+(?!\d))/g, "-")}
          marginBottom={true}
        />
        {props.info.status === "SUCCESS" && (
          <>
            <LabelGroup
              label="ePIN/Serial key"
              value={props.info.serialKey}
              marginBottom={true}
            />
            <button className="btn btn-sm btn-light mb-2 d-block px-3">
              Copy
            </button>
            {!isNaN(props.info.expiryDate) && (
              <LabelGroup
                label="Expiry Date"
                value={moment(props.info.expiryDate).format("DD-MMM-yyyy")}
              />
            )}
          </>
        )}
        {props.info.status === "PROCESSING" && (
          <>
            <div className="small my-3">
              Please wait 30 seconds to 2 minutes to complete processing.
            </div>
            <div className="clearfix">
              <button
                className="btn btn-success float-left"
                onClick={blockCheck}
                disabled={block > 0}
              >
                Check Status {block > 0 && `(${block})`}
              </button>
            </div>
          </>
        )}
      </div>
    </div>
  );
}

export default PurchaseEPinInfo;
